import React, { Component } from 'react';
import Select from 'react-select';
import BanNgoc from '../assets/img/ngo.png';
const options = [
    { value: '1', label: 'V Tr 1 sao' },
    { value: '2', label: 'V Tr 2 sao' },
    { value: '3', label: 'V Tr 3 sao' },
    { value: '4', label: 'V Tr 4 sao' },
    { value: '5', label: 'V Tr 5 sao' },
    { value: '6', label: 'V Tr 7 sao' },
    { value: '7', label: 'V Tr 7 sao' },
]
class MuaVang extends Component {
    render() {
        return (
            <div className={this.props.active ? 'shop-body-tab__left' : 'shop-body-tab__left active-tab'}>
                <div className="shop-body__top">
                    <div> K : 204 Ngc</div>
                </div>
                <div className="shop-body__bottom">
                    <div><img src={BanNgoc} /></div>
                    <div className="select-option">
                        <div className="select-title">My ch</div>
                        <Select
                            options={options}
                            defaultValue={[options[0]]}
                        />
                        <div className="input-vang">
                            <div className="select-title">Nhp s vng cn mua</div>
                            <input type="number" placeholder="100000" />
                        </div>
                        <button className="buy-btn">Mua</button>
                    </div>
                </div>
            </div>
        );
    }
}

export default MuaVang;



