import React, { Component } from 'react';
import './style/Shop.css';
import MuaVang from '../components/MuaVang'
import MuaNgoc from '../components/MuaNgoc'

class Shop extends Component {
  constructor(props) {
    super(props)
    this.state = {
      active: true
    }
  }

  changeTab = () => {
    this.setState({
      active: !this.state.active
    })
  }

  render() {
    return (
      <div id="shop" style={{textAlign: 'center'}}>
        <div className="shop-page">
          <div className="btn-buy">
            <div onClick={this.changeTab} className={this.state.active ? 'buy-gold active' : 'buy-gold'}>
              <span>Mua Vng</span>
            </div>
            <div onClick={this.changeTab} className={this.state.active ? 'buy-gem' : 'buy-gem active'}>
              <span>Mua Ngc</span>
            </div>
          </div>
          <div className="shop-body">
            <div className="title-shop">Dch V Bn Vng</div>
            <div className="shop-body-tab">
              <MuaVang active={this.state.active}/>
              <MuaNgoc active={this.state.active}/>
              <div className="border-wall"/>
              <div className="shop-body-tab__right">
                <div className="useful">M t :</div>
                <div>
                  <ul>
                    <li>Link hng dn mua vng ngc: <a>https://vangngoc.vn/bai-viet/huong-dan-mua-vang-ngoc.html</a></li>
                    <li>Link Facebook ca mnh: <a>http://www.facebook.com/keomut.tuong</a></li>
                    <li>S uy tn ca website: <a>http://vangngoc.vn/bai-viet/thong-tin-su-uy-tin-cua-website.html</a></li>
                    <li>Nu bn thc mc v s uy tn, Hy gi: 016888.780.75  gp Dng 20 tui</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Shop;



